    <el-header style="height: 80px">
      <el-upload action="https://jsonplaceholder.typicode.com/posts/" multiple :show-file-list="false" style="display: inline-block">
        <el-button type="primary" style="margin:10px 10px 0 0"><i class="fa fa-upload fa-lg" style="margin-right:10px"></i>上传</el-button>
      </el-upload>
      <button class="primary-button" @click="AddFolder"><i class="fa fa-plus fa-lg" style="margin-right:10px"></i>新建文件夹</button>
      <button class="primary-button" ><i class="fa fa-download fa-lg" style="margin-right:10px"></i>下载</button>
      <div id="file-position">
        <span id="return" @click="pageReturn()" v-if="this.$store.state.fileposition[this.$store.state.fileposition.length-1]!='全部文件'">返回上一级</span>
        <span v-if="this.$store.state.fileposition[this.$store.state.fileposition.length-1]!='全部文件'">|</span> 
        <span @click="pageReturnAll()" >全部文件</span>
      </div>
