<script>
import MyNav from './components/MyNav'
import mainrow from './components/main-row'
import mainrowtest from './components/main-row-test'
export default {
   data() {
      return {
        
      }
    },
    methods: {
      pageReturn() {
        this.$store.state.fileposition.pop();
        var lastarr = this.$store.state.fileposition[this.$store.state.fileposition.length-1]
        console.log(lastarr)
        this.$store.commit("folderClick",lastarr);
      },
      pageReturnAll() {
        this.$store.state.fileposition = ['全部文件'];
        var lastarr = this.$store.state.fileposition[this.$store.state.fileposition.length-1]
        console.log(lastarr)
        this.$store.commit("folderClick",lastarr);
      },
      AddFolder(){
        if(this.$store.state.allowClick){
          this.$store.state.data.push({
            type: 'folder',
            name: '新建文件夹',
            size: '-',
            date: this.getDate(),
            edit: true,
            hover: false,
          })
          this.$store.state.Formername = '新建文件夹';
          this.$store.state.allowClick = false;
          this.$store.state.allowHover = false;
        }
      },
      getDate(){
        var date = new Date();
        var year = date.getFullYear();
        var month = date.getMonth()+1;
        var day = date.getDate();
        if(month<=9) month = '0'+month;
        if(day<=9) day = '0'+day;
        var nowday = year + '-'  + month + '-'  + day;
        return nowday;
      },
    },
    components: {
      MyNav,mainrow,mainrowtest
    },
    created: function(){
      console.log(document.documentElement.clientHeight)
    }
}
</script>
